import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import { isSocialWall, isTagEmbed, isTagShop, isTagbox, isEmbed } from "../../../../../actions/api";
import ShareOnHover from "../../../Elements/ShareOnHover/ShareOnHover";
import CTAButton from "../../../Elements/CTA/CtaButton";
import Rating from "../../../Elements/Rating/Rating";
import SocialAction from "../../../Elements/SocialAction/SocialAction";
import { IS_THEME } from "../../../../../utils";
const Card = ({ itemData, personalization, adjustWidth, ThemeRule, clickToShowPopUp, itemIndex, wallID, ownerId, onClickToCTA, wall }) => {
  const cardSize = { width: `${adjustWidth}%`, padding: personalization.padding / 2 };
  const mediaType = (itemData.type === 2 || itemData.type === 3 || itemData.type === 4 || itemData.type === 5) ? true : false;
  const rating = (itemData.rating > 0) ? true : false;
  const ctaStatus = (itemData.cta && Object.keys(itemData.cta).length > 0 && itemData.cta.status) ? true : false;

  const isProduct = itemData.ugc_products && itemData.ugc_products.length > 0 ? true : false
  const isShopIcon = (isProduct && itemData.hotspot === 0) ? true : false
  const isHotspot = itemData.hotspot && isProduct ? true : false
  const outerpadding = isSocialWall ? { padding: ThemeRule.spacing } : null

  return <div id={`tb-cp-post-${itemData.id}`} className={`tb_w_f_post_wrapper`} style={cardSize} tb-network={itemData.network.id} tb-product={isShopIcon ? 1 : isHotspot ? 2 : 0}>
    <div className="tb_w_f_post_in" onClick={clickToShowPopUp(itemIndex, itemData)} style={outerpadding}>
      <div className="tb_w_f_post_media_wrapper">
        {itemData.share.status ? <ShareOnHover share={itemData.share} shareClass={'tb_w_f_share_container'} item={itemData} wallID={wallID} ownerId={ownerId} /> : ''}
        {mediaType ? <Media key={`img${itemIndex}_${itemData.id}`} itemData={itemData} wallID={wallID} ownerId={ownerId} ThemeID={personalization.widgetTheme} wall={wall} /> : ''}
        {isTagEmbed && rating ? <div className="tb_w_f_rating__"><Rating rating={itemData.rating} network={itemData.network} /> </div> : ''}
        {ctaStatus ? <div className="tb_w_f_post_cta"><CTAButton ctaClass={'tb_w_f_post_cta_btn'} cta={itemData.cta} item={itemData} onClickToCTA={onClickToCTA} /> </div> : ''}
      </div>
      <div className="tb_w_f_contant_wrapper">
        <AuthorDetails ownerId={ownerId} postTime={itemData.createdAt} author={itemData.author} network={itemData.network} font={itemData.font} personalization={personalization} ThemeRule={ThemeRule} />
      </div>

      {ThemeRule.socialAction && !IS_THEME(63) ? <SocialAction itemData={itemData} ThemeRule={ThemeRule} ThemeID={personalization.widgetTheme} actionClass={`tb_w_f_social_action__ ${isTagEmbed ? 'tb_w_f_social_action_bg' : ''}}`} /> : ''}
    </div>
  </div>
}
export default Card