import React, { Component } from "react";
import Card from "./Elements/Card";
import { connect } from 'react-redux';
import Masonry from 'react-masonry-component';
import { managePostHeight } from '../../../../actions/themeActions';
import "./scss/Focus.scss";
import ScrollAnimation from "../../../Components/ScrollAnimation";
const FocusExtend = ({ loadData, postData, completeDataObject, BrandingPosition, clickToShowPopUp, wall, adjustWidth, onClickToCTA }) => {
  return loadData ?
    ((postData?.length > 0)) && postData.map((item, index) => {
      const cardData = completeDataObject[item];
      const IsBrandingCard = (BrandingPosition === index) ? false : false
      IsBrandingCard ? BrandingPosition += parseInt(index) + 10 : parseInt(BrandingPosition)
      return <Card ownerId={wall.Wall.owner} key={`Classic_Card_${index}_${cardData.id}`} itemData={cardData} itemIndex={index} adjustWidth={adjustWidth} personalization={wall.Personalization} ThemeRule={wall.ThemeRule} wallID={wall.Wall.id} clickToShowPopUp={clickToShowPopUp} onClickToCTA={onClickToCTA} wall={wall} />
    }) : null
}


class Focus extends Component {
  state = {
    postData: [],
    loadData: 1
  }
  componentDidMount() {
    const { postData, heightUpdate } = this.props;
    this.setState({ postData, loadData: 1 }, () => setTimeout(() => heightUpdate(), 1000))
  }


  componentDidUpdate(prevProps, prevState) {
    if (prevProps.postData !== this.props.postData) {
      this.props.heightUpdate();
      return true
    }
    else return false
  }

  render() {
    const { postData, completeDataObject, adjustWidth, wall, clickToShowPopUp, onClickToCTA } = this.props;
    const { loadData } = this.state;
    var BrandingPosition = 4

    return <ScrollAnimation wall={wall} parentId="tb_marquee" renderID="focus">
      <div id="tb_marquee">
        <Masonry
          id="focus"
          className={`tb_w_f_post_container`}
          tabIndex='0'
          aria-label={`There are ${(postData && postData.length > 0) ? postData.length : 0} posts in the feed`} role="feed"
          elementType={'div'}
          options={{
            transitionDuration: 0,
            percentPosition: true,
            itemSelector: '.tb_w_f_post_wrapper',
            horizontalOrder: false,
            resize: true,
          }}
          disableImagesLoaded={false}
          updateOnEachImageLoad={false}
          imagesLoadedOptions={{}}
          enableResizableChildren={true}>
          <FocusExtend loadData={loadData} wall={wall} clickToShowPopUp={clickToShowPopUp} postData={postData} completeDataObject={completeDataObject} BrandingPosition={BrandingPosition} adjustWidth={adjustWidth} onClickToCTA={onClickToCTA} />
        </Masonry>
      </div>
    </ScrollAnimation>
  }
}

const mapStateToProps = state => {
  return {}
}
const mapDispatchToProps = dispatch => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Focus)