import Author from "../../../Elements/Author/Author";
import Time from "../../../Elements/Time/Time";
import Network from "../../../Elements/Network/Network";
import { IS_THEME } from "../../../../../utils";
const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  const icnshwhid = (ThemeRule.socialAction && IS_THEME(63)) ? true : false
  return <div className="tb_w_f_author_wrapper">
    <div className="tb_w_f_author">
      {postAuthor ? <Author author={author} network={network} authorClass={'tb_w_f_author_profile'} /> : ''}

      <div className="tb_w_f_author_info">
        {postAuthor ? <div className="tb_w_f_authorname">{author.name}</div> : ''}
        <div className="tb_w_f_post_info">
          {postAuthor ? <div className="tb_w_f_username">{username}</div> : ''}
          {authorSeprator ? <div className="tb_w_f_seprator"> </div> : ''}
          {personalization.postTime ? <Time postTime={postTime} timeClass={`tb_w_f_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> : ''}
        </div>
      </div>
    </div>
    {icnshwhid ? <div className="tb_w_f_social_">
      <Network networkClass={'tb_w_f_social_ico'} network={network} ThemeRule={ThemeRule} isDefault={ThemeRule.iconType === 1 ? true : false} />
    </div>  : ''}
  </div>
}
export default AuthorDetails